































import {
    mapState,
    mapActions
} from 'vuex'
export default {
	data() {
		return {
			ing: 'https://t1.huanqiu.cn/85684d610ae55f35b206de48f7f4ec58.jpg',
			username: '', // 13814699340 13814699341 777  123  是配送的 2  888 123  是安装维修 1 3    1 123 2 123 购货 4
			password: '', // 123
			showPassword: true
		}
	},
	components: {
	},
	computed: {
		...mapState({
		  urlPre: state => state.urlPre
		})
	},
	onLoad() {
	},
	methods: {
		...mapActions([
		  'updateUserInfo',
		  'changeTabIndex'
		]),
		changePassword: function() {
			this.showPassword = !this.showPassword
		},
		login: function() {
			// 校验
			if (this.username == '') {
				uni.showToast({
				    image: '/static/icons/attention.png',
				    title: '用户名不能为空!'
				})
				return false
			}
			if (this.password == '') {
				uni.showToast({
				    image: '/static/icons/attention.png',
				    title: '密码不能为空!'
				})
				return false
			}
			this.loginRequest()
		},
		loginRequest: function() {
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url: this.urlPre + '/page/login.do',
				data: {
					username:this.username,
					password:this.password,
				},
				success: (res) => {
					switch (res.data.result) {
						case 1:
							// 登陆后初始化tabIndex
							this.changeTabIndex(0)
							// 更新用户信息
							this.updateUserInfo({'userId': res.data.id, 'userRole': res.data.ftype, 'userName': res.data.username, 'purchaseUnit': res.data.gouhuodanwei?res.data.gouhuodanwei:'' })
							// 根据用户类型跳转
							if (res.data.ftype == 4) {
								// 购货
								uni.redirectTo({
									url: '/pages/search/purchaseSearch'
								})
							} else {
								uni.redirectTo({
									url: '/pages/orderList/orderList'
								})
							}
							break
						case 2:
							uni.hideLoading()
							uni.showToast({
							    image: '/static/icons/attention.png',
							    title: '密码错误!'
							})
							break
						  case 0:
							uni.hideLoading()
							uni.showToast({
							    image: '/static/icons/attention.png',
							    title: '用户不存在!'
							})
							break
						  default:
							uni.hideLoading()
							uni.showToast({
							    image: '/static/icons/attention.png',
							    title: '服务器繁忙!'
							})
					}
				},
				fail: (err) => {
					console.log('request fail', err)
					uni.hideLoading()
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
				}
			})
		}
	}
}
